import React, {
  useCallback, useEffect, useRef, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, Center, Heading, Input, HStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import InfiniteScroll from 'react-infinite-scroll-component';
import Icon from '../../../components/Icon';
import style from '../CardList/CardList.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { listAllSentCards } from '../../../store/middlewares/card';
import { apiErrorHandler, preloadImage } from '../../../utils/general';
import SentCard from '../../../components/SentCard';
import { getbusinessById } from '../../../store/middlewares/business';
import { updateSelectedBusiness } from '../../../store/actionCreators/business';
import { useDebouncedEffect } from '../../../hooks/app';
import SkeletonLoader from '../../../components/SkeletonLoader';
import Loader from '../../../components/Loader';

const showSearch = true;
const showAddCard = true;
const cardsPerView = 20;
const pagination = true;
const showExtendedDetails = true;
const showCompactView = false;
/**
 * SentCard list
 * @returns {JSX.Element}
 */
const SentCardList = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const page = useRef(1);

  const isSeachChanged = useRef(false);
  const [searchKeyword, updateSearch] = useState('');
  const { sentCards, cardLoading } = useAppSelector((store) => store.card, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const business = useAppSelector((store) => store.business.selectedBusiness, shallowEqual);

  const [cards, updateCards] = useState(sentCards.data);

  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);

  const getSentCards = useCallback(() => {
    if (account) {
      dispatch(listAllSentCards(account.id, cardsPerView, (page.current - 1) * cardsPerView, searchKeyword.length === 0 ? undefined : searchKeyword))
        .then((res) => {
          updateCards(res.data);
        })
        .catch(apiErrorHandler);
    }
  }, [account, cardsPerView, page, searchKeyword]);

  useEffect(() => {
    if (account && account.businessId !== '' && !business) {
      dispatch(getbusinessById(account.businessId))
        .then((b) => dispatch(updateSelectedBusiness(b)));
    }
  }, [account, business]);

  const reloadCardList = useCallback(() => {
    /*
    page.current = 1;
    getSentCards();
    */
    // TODO: Temprory hack
    if (searchKeyword && searchKeyword.length !== 0) {
      const filteredCards = sentCards.data.filter((c) => c.card.last4.includes(searchKeyword));
      updateCards(filteredCards);
    } else {
      updateCards(sentCards.data);
    }
  }, [getSentCards, page, sentCards, searchKeyword]);

  useEffect(() => {
    // preload art images
    preloadImage(programConfig?.brand.cardArtVirtual);
    getSentCards();
  }, [account]);

  useDebouncedEffect(() => {
    if (isSeachChanged.current) {
      reloadCardList();
    }
  }, 1000, [isSeachChanged, reloadCardList]);

  const addSentCardDOM = (isHorizontal = false) => (
    <Box
      className={`${style.cardList__add_container} default-bg ${isHorizontal ? style.cardList__add_container_horizontal : ''}`}
      maxW={cards.length === 0 ? '320px' : 'none'}
      onClick={() => {

      }}
    >
      <Center className={style.cardList__add}>
        <Icon icon="icon-add" className="icon-customizable" size={40} />
        <Text variant="regular" pt="10px">{t('cards_list.addAVisaCard')}</Text>
      </Center>
    </Box>
  );

  const cardList = (isHorizontal = false) => (
    <Box className={style.autoCardHeight}>
      {showAddCard && showCompactView
        ? addSentCardDOM(isHorizontal) : null }
      {cards.map((c) => (
        <Box
          border={showExtendedDetails ? '1px solid var(--border-color-1)' : 'none'}
          key={c.card.id}
          className={style.cardList__card_container}
        >
          <SentCard data={c.card} onRefresh={reloadCardList} />
        </Box>
      ))}
    </Box>
  );

  return (
    <Center className="page" height="100%">
      {cardLoading && page.current === 1 && !isSeachChanged.current
        ? (
          <SkeletonLoader name="CardList" showHeader={!showCompactView} />
        )
        : (
          <Box width="100%" flexDirection="column" height="100%" display="flex">
            <Box p="15px">
              <HStack width="100%" justifyContent="space-between" mb="16px">
                {/* Heading */}
                <Heading variant="navTitle" mr="auto">{t('cards_list.sentCards')}</Heading>
              </HStack>
              {
        showSearch ? (
          <HStack mt="16px">
            {/* Input and search */}
            <Box className={style.cardList__search}>
              <Icon icon="icon-search" className={style.cardList__search__icon} size={18} />
              <Input
                placeholder={t('cards_list.searchCards')}
                variant="primary"
                className={style.cardList__search__input}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  isSeachChanged.current = true;
                  updateSearch(e.target.value);
                }}
                value={searchKeyword}
              />
            </Box>

            {showAddCard && showSearch && !showCompactView ? (
              <Center
                className={`${style.contactAddContainer} button-icon-bg`}
                onClick={() => {

                }}
              >
                <Icon className="dark-color" icon="icon-plus" size={16} />
                <Text className={`${style.contactAddTxt} dark-color`}>
                  {t('select_contact.add')}
                </Text>
              </Center>
            ) : null}
          </HStack>
        ) : null
      }
            </Box>

            {/* Cards */}
            {cardLoading && isSeachChanged.current ? <SkeletonLoader name="CardList" showHeader={false} />
              : (
                <div
                  className={style.scrollableDiv}
                  id="scrollableDiv"
                >
                  <InfiniteScroll
                    dataLength={cards.length}
                    next={() => {
                      page.current += 1;
                      getSentCards();
                    }}
                    hasMore={pagination && sentCards.data.length < sentCards.total && sentCards.total !== 0}
                    loader={<Center my={3}><Loader showModal={false} show /></Center>}
                    endMessage={(
            cardLoading ? null
              : (
                <Center py={2}>
                  <Text variant="regular" className="text-light" textAlign="center">
                    {t(sentCards.total === 0 ? (showAddCard && showCompactView ? '' : 'cards_list.noData') : 'cards_list.noMoreCards')}
                  </Text>
                </Center>
              )
          )}
                    scrollableTarget="scrollableDiv"
                  >
                    {cardList()}
                  </InfiniteScroll>
                </div>
              )}
          </Box>
        )}

    </Center>
  );
};

export default SentCardList;
