import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, HStack, Text, Image, Button
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useTranslation } from 'react-i18next';
import style from './Card.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../hooks/store';
import ConfirmModal from './ConfirmModal/ConfirmModal';
import { sentCardDelete } from '../store/middlewares/card';
import { apiErrorHandler, showNotification } from '../utils/general';
import CardImg from '../assets/images/cards/card-dummy.svg';

type Props = {
    data: any,
    onRefresh: () => void;
}

const SentCard:React.FC<Props> = React.memo(({ data, onRefresh }) => {
  const c = data;
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const [showModal, setShowModal] = useState(false);

  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);
  const cardImg = useMemo(() => programConfig?.brand.cardArtVirtual, [c, programConfig]);

  const deleteCard = useCallback(() => {
    dispatch(sentCardDelete(c.id))
      .then(() => {
        setShowModal(false);
        showNotification('Success', 'Sent card successfully cancelled', 'success');
        onRefresh();
      })
      .catch(apiErrorHandler);
  }, [sentCardDelete, showNotification, apiErrorHandler]);

  return (
    <Box className={`${style.card_image_info} ${style.cardList__card_image_info_white} ${style.sent_card} ${c.cardStatus !== 'active' ? style.is_cancelled : ''}`}>
      <Image className={style.card_image} alt="logo" src={cardImg} fallbackSrc={CardImg} data-testid="card-img" />
      <Box className={style.card_details}>
        {/* <Text color="#000" fontWeight="700" fontSize="10px">BUSINESS DEBIT</Text> */}
        <Box pt="14px" pb="18px" mb="auto">
          <Text color="#ffffff" fontWeight="500" fontSize="10px">{t('cards_list.virtual')}</Text>
          <Text color="#A1A1A1" fontSize="10px">
            &#36;
            {' '}
            {c.availableLimit}
          </Text>
        </Box>
        <Text color="#ffffff" fontWeight="700" fontSize="15px" pb="8px">
          **** **** ****
          {' '}
          {c.last4}
        </Text>
        <HStack>
          <Box alignItems="flex-start" mr="40px">
            <Text color="#A1A1A1" fontSize="10px">{t('cards_list.expDate')}</Text>
            <Text color="#ffffff" fontSize="13px">
              {c.expiryMonth}
              /
              {c.expiryYear}
            </Text>
          </Box>
          <Box alignItems="flex-start">
            <Text color="#A1A1A1" fontSize="10px">{t('cards_list.cvv')}</Text>
            <Text color="#ffffff" fontSize="13px">
              {c.expiryYear}
            </Text>
          </Box>
        </HStack>
        <Box mt="auto">
          <Text color="#ffffff" fontWeight="700" fontSize="9px">
            {c.label}
          </Text>
        </Box>
      </Box>
      {
        c.cardStatus === 'active' && (
        <>
          <Box pt="14px" pb="18px" className={style.cancel_card}>
            <Button width="100%" variant="primar" onClick={() => { setShowModal(true); }}>
              { t('cards_list.cancelCard') }
            </Button>
          </Box>
          <ConfirmModal
            showModal={showModal}
            title="Cancel card"
            description="This cannot be undone."
            cancelButton="No"
            submitButton="Yes"
            onCancel={() => { setShowModal(false); }}
            onOk={deleteCard}
          />
        </>
        )
      }
    </Box>
  );
});

export default SentCard;
